import React from "react"
import styles from "../styles/team.module.css"

export default (props) => (
    <div className={props.classname}>
        <div className={styles.link}>
            <img src={props.background} alt="" className={props.widthname}/>
        </div>
        <div className={`${styles.boldText} ${props.topname}`}> {props.btext} </div>
        <div className={`${styles.text} ${props.topname}`}> {props.ftext} </div>
        <div className={`${styles.content} ${props.topname}`}> {props.children} </div>
    </div>
)